/* eslint-disable no-unused-vars */
import React from 'react'
import md5 from 'md5'
import './login.css'
import 'react-router-dom'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import axios from 'axios'
import Card from './Card'

import { useEffect } from 'react'
import { useState } from 'react'
function Login(props) {
    const defaulturl = "data:image/jpeg;base64,"
    function recheck()
    {
        let x=document.cookie.split(", ")
        x.forEach(item=>{
            if(item.split("=")[0]==="auth")
            {
                props.handler(parseInt(item.split("=")[1]))
            }
        })
    }
    axios.defaults.baseURL="https://adi-movie-backend.herokuapp.com"
    //axios.defaults.baseURL="http://localhost:5000"
    
    const [defaultfile,setdefaultfile]=useState()
    useEffect(function(){
        //console.log(document.getElementById("imgu").style.width)
    document.getElementById("imgu").style.height=document.getElementById("imgu").style.width="100%"
    setdefaultfile(document.getElementById("image").files)
    },[])
    //console.log(window.location.href==='http/#/')
    function validate_email(email) {
        var mailformat = /^\w+@[a-zA-Z_]+?\.[a-zA-Z]{2,3}$/;
        //let email="sp479644@gmail.com"
        console.log(email)
        if (email.match(mailformat)) {

            return true;
        }
        else {
            console.log("validation failed email")
            return false;
        }

    }
    function validate_pass(pass) {
        var passw = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#\$%\^&\*])(?=.{8,})/;
        if (pass.match(passw)) {

            return true;
        }
        else {
            console.log("validation failed pass")
            return false;
        }
    }
    var files = null;

    async function register() {
        let email = document.getElementById("exampleInputEmail2").value
        let pass = document.getElementById("exampleInputPassword2").value
        console.log(email,md5(pass))

        if (validate_email(email) && validate_pass(pass) && files) {
            console.log(email, pass, files)
            const formData = new FormData();
            formData.append('image', files)
            formData.append('email', email)
            formData.append('pass', md5(pass))
            console.log(formData)
            //await axios.post("/register",{"email":email,"pass":pass,"img":files})
            await axios.post("/register", formData, {
                withCredentials:true,
                headers: {
                    'Content-Type': 'multipart/form-data'
                },
                onUploadProgress:ProgressEvent=>{
                    console.log("uploading: "+parseInt(Math.round((ProgressEvent.loaded*100)/ProgressEvent.total)))
                }
            })

                .then(res => {
                   
                    console.log(res.data.res)
                    if (res.data["res"] === "Registration Successful!") {
                        //let imgurl = "data:image/png;base64,"+res.data.file
                        //.getElementById("imgu").src = imgurl
                        setTimeout(() => {
                            console.log(defaultfile)
                            console.log("time up")
                            document.getElementById("imgu").src = defaulturl
                            document.getElementById("image").files =defaultfile
                            document.getElementById("exampleInputEmail2").value = ""
                            document.getElementById("exampleInputPassword2").value = ""
                            document.getElementById("regform").scrollTop = 0
                        }, 4000);


                    }

                })
                .catch(err => console.log(err))
        }
        else {
            console.log("validation of email and password failed")
        }
    }
    async function login() {
        let email = document.getElementById("exampleInputEmail1").value
        let pass = document.getElementById("exampleInputPassword1").value
        console.log(email, md5(pass))
        if (validate_email(email) && validate_pass(pass)) {


            await axios.post("/login/in", { "email": email, "pass": md5(pass) },{withCredentials:true})
                .then(res => {
                    //recheck()
                    props.handler(res.data.auth)
                    console.log(res.data)
                    console.log(res.data.res)
                    if (res.data["res"] === "login successful") {
                        document.getElementById("exampleInputEmail1").value = ""
                        document.getElementById("exampleInputPassword1").value = ""
                       // window.location = "#/user/" + res.data.id

                    }
                })
                .catch(err => console.log(err))
        }
        else {
            console.log("validation of email and password failed")
        }

    }
    async function checkExist(s)
    {
        if(validate_email(s))
        {
            return  axios.post("/login/checkEmail",{"email":s},{withCredentials:true}).then(result=>{
                console.log(result.data)
                document.getElementById("exampleInputEmail2").style.color=(result.data.checkResult)?"green":"red";
                return (result.data.checkResult)
            })
            
            
        }
        document.getElementById("exampleInputEmail2").style.color="red"
        return false;
    }
    function upload(e) {
        files = e.target.files[0]
        let  reader=new FileReader()
        reader.onload=function(){
            console.log(reader.result)
            document.getElementById("imgu").src=reader.result
        }
        reader.readAsDataURL(files)
        reader.onerror=function(err){
            console.log(err)
        }
        console.log(files)

    }
    return (
        <>
            <div className="logform">
                <div className="regform" id="regform">
                    <div className="login">
                        <h5 className="rep">Login</h5>
                        <br />
                        <div>
                            <div className="form-group">
                                <label htmlFor="exampleInputEmail1">Email address</label>
                                <input type="email" className="form-control" id="exampleInputEmail1" onChange={(e)=>{e.target.style.color=(validate_email(e.target.value))?"green":"red";}} aria-describedby="emailHelp" placeholder="Enter email" />
                                <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleInputPassword1">Password</label>
                                <input type="password" className="form-control" id="exampleInputPassword1" onChange={(e)=>{e.target.style.color=(validate_pass(e.target.value))?"green":"red";}} placeholder="Password" />
                                <small id="passHelp" className="form-text text-muted">password between 7 to 16 characters which contain only characters, numeric digits and underscore and first character must be a letter.</small>
                            </div>

                            <button className="btn btn-primary" id="login" onClick={login}>Submit</button>
                        </div>
                    </div>
                    <div>

                    </div>
                    <div className="reg" id="reg">
                        <h5 className="rep">Register</h5>
                        <br />
                        <div>
                            <div className="form-group">
                                <label htmlFor="exampleInputEmail1">Email address</label>
                                <input type="email" className="form-control" onChange={(e)=>{let x=checkExist(e.target.value)}} id="exampleInputEmail2" aria-describedby="emailHelp" placeholder="Enter email" />
                                <small id="emailHelp" className="form-text text-muted">We'll never share your email with anyone else.</small>
                            </div>
                            <div className="form-group">
                                <label htmlFor="exampleInputPassword1">Password</label>
                                <input type="password" className="form-control" id="exampleInputPassword2" onChange={(e)=>{e.target.style.color=(validate_pass(e.target.value))?"green":"red";}} placeholder="Password" />
                                <small id="passHelp" className="form-text text-muted">password between 7 to 16 characters which contain only characters, numeric digits and underscore and first character must be a letter.</small>
                            </div>
                            <div className="form-group">
                                <label htmlFor="Image">Upload Your Image</label>
                                <input type="file" name="image" id="image" className="form-control overflow-auto" onChange={upload} />
                            </div>
                            <center>
                            <button className="btn btn-primary" id="register" onClick={register}>Submit</button>

                            </center>



                        </div>
                        <div className="upimg" id="upimg" style={{position:"relative"}}>
                            <img src={defaulturl} alt="" style={{ position:"absolute",top:"20px",border:"5px solid black"}} id="imgu" />
                        </div>
                    </div>
                </div>
            </div>
        </>
    )
}
export default Login